import classes from './Auth.module.css'
import Button from '../../components/UI/button/Button'
import Input from '../../components/UI/Input/Input'
import React from 'react'
import is from 'is_js'
import axios from 'axios'

function Auth() {
    const [formControls, setFormControls] = React.useState({
        email: {
            value: '',
            type: 'email',
            label: 'Email',
            errorMessage: '  email',
            valid: false,
            touched: false,
            validation: { required: true, email: true },
        },
        password: {
            value: '',
            type: 'password',
            label: '',
            errorMessage: '  password',
            valid: false,
            touched: false,
            validation: { required: true, minLength: 6 },
        },
    })
    const [isFormValid, setFormValid] = React.useState(false)

    const loginHandler = async () => {
        const authData = {
            email: formControls.email.value,
            password: formControls.password.value,
            returnSecureToken: true,
        }

        try {
            const response = await axios.post(
                'https://identitytoolkit.googleapis.com/v1/accounts:signInWithPassword?key=AIzaSyAJrNlO6-uX3t3gO84J6oNu_IWWTsnNbo4'
            )
            console.log(response.data)
        } catch (e) {
            console.log(e)
        }
    }
    const registerHandler = async () => {
        const authData = {
            email: formControls.email.value,
            password: formControls.password.value,
            returnSecureToken: true,
        }

        try {
            const response = await axios.post(
                'https://identitytoolkit.googleapis.com/v1/accounts:signUp?key=AIzaSyAJrNlO6-uX3t3gO84J6oNu_IWWTsnNbo4'
            )
            console.log(response.data)
        } catch (e) {
            console.log(e)
        }
    }

    const submitHandler = (event) => {
        event.preventDefault()
    }

    const validateControl = (value, validation) => {
        if (!validation) {
            return true
        }

        let isValid = true
        if (validation.required) {
            isValid = value.trim() !== '' && isValid
        }

        if (validation.email) {
            isValid = is.email(value) && isValid
        }

        if (validation.minLength) {
            isValid = value.length >= validation.minLength && isValid
        }
        return isValid
    }

    const onChangeHandler = (event, controlName) => {
        const newFormControls = { ...formControls }
        const control = { ...formControls[controlName] }

        control.value = event.target.value
        control.touched = true
        control.valid = validateControl(control.value, control.validation)

        newFormControls[controlName] = control

        let isFormValid = true

        Object.keys(newFormControls).forEach((name) => {
            isFormValid = newFormControls[name].valid && isFormValid
        })

        setFormControls(newFormControls)
        setFormValid(isFormValid)
    }

    const renderInputs = Object.keys(formControls).map((controlName, index) => {
        const control = formControls[controlName]
        return (
            <Input
                key={controlName + index}
                type={control.type}
                value={control.value}
                valid={control.valid}
                touched={control.touched}
                label={control.label}
                errorMessage={control.errorMessage}
                shouldValidate={!!control.validation}
                onChange={(event) => onChangeHandler(event, controlName)}
            />
        )
    })
    return (
        <div className={classes.Auth}>
            <div>
                <h1></h1>

                <form className={classes.AuthForm} onSubmit={submitHandler}>
                    {renderInputs}

                    <Button
                        type="success"
                        onClick={loginHandler}
                        disabled={!isFormValid}
                    >
                        
                    </Button>
                    <Button
                        type="primary"
                        onClick={registerHandler}
                        disabled={!isFormValid}
                    >
                        
                    </Button>
                </form>
            </div>
        </div>
    )
}

export default Auth
