import React from 'react'
import Button from '../../components/UI/button/Button'
import classes from './QuizCreator.module.css'
import { createControl, validate, validateForm } from '../../form/formFramework'
import Input from '../../components/UI/Input/Input'
import Select from '../../components/UI/Select/Select'
import axios from '../../axios/axios-quiz'

function createOptionControl(number) {
    return createControl({
        label: ` ${number}`,
        errorMessage: '    ',
        id: number
    }, {required: true})
}

function createFormControls() {
    return {
        question: createControl({
            label: ' ',
            errorMessage: '    '
        }, {required: true}),
        option1: createOptionControl(1),
        option2: createOptionControl(2),
        option3: createOptionControl(3),
        option4: createOptionControl(4)

    }
}

function QuizCreator() {

    const [quiz, setQuiz] = React.useState([])
    const [formControls, setFormControls] = React.useState(createFormControls())
    const [rightAnswerId, setRightAnswerId] = React.useState(1)
    const [isFormValid, setIsFormValid] = React.useState(false)

    const submitHandler = event => {
        event.preventDefault()
    }

    const addQuestionHandler = event => {
        event.preventDefault()
        const newQuiz = quiz.concat()
        const index = quiz.length + 1

        const { question, option1, option2, option3, option4} = formControls

        const questionItem = {
            question: question.value,
            id: index,
            rightAnswerId: rightAnswerId,
            answers: [
                {text: option1.value, id: option1.id},
                {text: option2.value, id: option2.id},
                {text: option3.value, id: option3.id},
                {text: option4.value, id: option4.id},
            ]
        }

        quiz.push(questionItem)

        setQuiz(quiz)
        setFormControls(createFormControls())
        setIsFormValid(false)
        setRightAnswerId(1)
    }

    const createQuizHandler = async event => {
        event.preventDefault()

        try {
            await axios.post('/quizes.json', quiz)
            
            setQuiz([])
            setIsFormValid(false)
            setRightAnswerId(1)
            setFormControls(createFormControls())
        } catch(e) {
            console.log(e);
        }

    }

    const changeHandler = (value, controlName) => {
        const newFormControls = {...formControls}
        const control = { ...formControls[controlName]}

        control.touched = true
        control.value = value
        control.valid = validate(control.value, control.validation)

        newFormControls[controlName] = control

        setFormControls(newFormControls)
        setIsFormValid(validateForm(newFormControls))
    }

    const selectChangeHandler = event => {
        setRightAnswerId(+event.target.value)
    }

    const renderControls = Object.keys(formControls).map((controlName, index) => {
        const control = formControls[controlName]

        return (
        <React.Fragment key={index}>
        <Input
            label={control.label}
            value={control.value}
            valid={control.valid}
            shouldValidate={!!control.validation}
            touched={control.touched}
            errorMessage={control.errorMessage}
            onChange={event => changeHandler(event.target.value, controlName)}
        />
        { index === 0 ? <hr /> : null}
        </React.Fragment>
        )
    })

    const select = <Select
        label="  "
        value={rightAnswerId}
        onChange={selectChangeHandler}
        options={
            [
                {text: 1, value: 1},
                {text: 2, value: 2},
                {text: 3, value: 3},
                {text: 4, value: 4},
            ]
        }
    />

    return (
        <div className={classes.QuizCreator}>
            <div>
                <h1> </h1>

                <form onSubmit={submitHandler}>

                    {renderControls}
                    {select}

                    <Button type="primary" onClick={addQuestionHandler} disabled={!isFormValid}> </Button>
                    <Button type="success" onClick={createQuizHandler} disabled={quiz.length === 0}> </Button>
                </form>
            </div>
        </div>
    )
}

export default QuizCreator