import { NavLink } from 'react-router-dom'
import Backdrop from '../../UI/Backdrop/Backgrop'
import classes from './Drawer.module.css'

const links = [
    { to: '/', label: '', exact: true },
    { to: '/auth', label: '', exact: false },
    { to: '/quiz-creator', label: ' ', exact: false },
]

function Drawer(props) {
    const clickHandler = () => props.onClose()
    const renderLinks = () =>
        links.map((link, index) => (
            <li key={index}>
                <NavLink
                    to={link.to}
                    exact={link.exact.toString()}
                    className={({ isActive }) =>
                        isActive ? classes.active : null
                    }
                    onClick={clickHandler}
                >
                    {link.label}
                </NavLink>
            </li>
        ))
    const cls = [classes.Drawer]

    if (!props.isOpen) {
        cls.push(classes.close)
    }
    return (
        <>
            <nav className={cls.join(' ')}>
                <ul>{renderLinks()}</ul>
            </nav>
            {props.isOpen && <Backdrop onClick={props.onClose} />}
        </>
    )
}

export default Drawer
