import classes from './Quiz.module.css'
import React from 'react'
import ActiveQuiz from '../../components/ActiveQuiz/ActiveQuiz'
import FinishedQuiz from '../../components/FinishedQuiz/FinishedQuiz'
import axios from '../../axios/axios-quiz'
import Loader from '../../components/UI/Loader/Loader'
import { useParams } from 'react-router-dom'


function Quiz(props) {

    const {id} = useParams()

    React.useEffect(() => {
        async function getData() {
            const response = await axios.get(`/quizes/${id}.json`)
            const quiz = response.data

            setQuiz(quiz)
            setLoading(false)
        } 

        getData()
    }, [])

    
    const [quiz, setQuiz] = React.useState([])

    const [activeQuestion, setActiveQuestion] = React.useState(0)

    const [answerState, setAnswerState] = React.useState(null)

    const [isFinished, setIsFinished] = React.useState(false)

    const [results, setResults] = React.useState({})
    
    const [loading, setLoading] = React.useState(true)

    const isQuizFinished = () => activeQuestion + 1 === quiz.length

    const onAnswerClickHandler = answerId => {
        if(answerState && answerState[answerId] === 'success') {
            return
        }

        const question = quiz[activeQuestion]

        if(question.rightAnswerId === answerId) {
            if(!results[question.id]) {
                setResults(prevResults => ({...prevResults, [question.id]: 'success'}))
            }

            setAnswerState(prevAnswerState => ({...prevAnswerState, [answerId]: 'success'}))

            const timeout = window.setTimeout(() => {
                if(isQuizFinished()) {
                    setIsFinished(true)
                } else {
                    setActiveQuestion(prevActiveQuestion => ++prevActiveQuestion)
                    setAnswerState(null)
                }

                window.clearTimeout(timeout)
            }, 1000)
        } else {
            setResults(prevResults => ({...prevResults, [question.id]: 'error'}))
            setAnswerState(prevAnswerState => ({...prevAnswerState, [answerId]: 'error'}))
        }
    }

    const retryHandler = () => {
        setActiveQuestion(0)
        setAnswerState(null)
        setIsFinished(false)
        setResults({})
    }

    return (
        <div className={classes.Quiz}>
            <div className={classes.QuizWrapper}>
                <h1>   </h1>


                {loading ? <Loader /> : 
                
                isFinished ? 
                    <FinishedQuiz
                        results={results} 
                        quiz={quiz}
                        onRetry={retryHandler}
                    /> 
                    : 
                    <ActiveQuiz 
                    answers={quiz[activeQuestion].answers} 
                    question={quiz[activeQuestion].question}
                    onAnswerClick={onAnswerClickHandler}
                    quizLength={quiz.length}
                    answerNumber={activeQuestion + 1}
                    state={answerState}
                    />
                    
                    }
                
            </div>
        </div>
    )
}



export default Quiz